*, :before, :after {
  box-sizing: border-box;
  border: 0;
  margin: 0;
  padding: 0;
}

h1, h2, h3, h4, h5, h6, p {
  margin: 0;
}

a {
  color: inherit;
  text-decoration: none;
}

ul, ol {
  margin: 0;
  padding-left: 0;
  list-style: none;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

button {
  cursor: pointer;
  border: none;
  padding: 0;
}

input, textarea, button {
  font-family: inherit;
}

:root {
  --button-green: #bbf330;
  --dark-green: #7eb101;
  --white: #fefefe;
  --yellow-main: #ffe550;
  --black: #2c2c2c;
  --shadow-fruit: radial-gradient( 50% 50% at 50% 50%, #2f583f 0%, rgba(105, 105, 105, 0) 100% );
  --transition: .4s ease;
}

body {
  color: var(--black);
  height: 100%;
  font-family: Roboto;
  font-size: 34px;
  font-style: normal;
  font-weight: 400;
  line-height: 1.29;
}

.container {
  max-width: 300px;
  margin: 0 auto;
  padding: 0 20px;
}

body::-webkit-scrollbar {
  width: 10px;
}

body::-webkit-scrollbar-thumb {
  background: var(--dark-green);
  border: 1px solid var(--white);
  border-radius: 20px;
}

body::-webkit-scrollbar-track {
  background-color: var(--yellow-main);
}

@media screen and (min-width: 428px) {
  .container {
    max-width: 396px;
    padding: 0 16px;
  }
}

@media screen and (min-width: 768px) {
  .container {
    max-width: 768px;
    padding: 0 32px;
  }
}

@media screen and (min-width: 1280px) {
  .container {
    max-width: 1280px;
    padding: 0 16px;
  }
}

.header {
  width: 100%;
  z-index: 10;
  position: fixed;
  top: 0;
  left: 0;
}

.header:before {
  content: "";
  width: 100%;
  height: 100%;
  background-color: var(--dark-green);
  position: absolute;
  top: 0;
  left: 0;
}

.header-body {
  z-index: 2;
  height: 72px;
  justify-content: space-between;
  align-items: center;
  display: flex;
  position: relative;
}

.header-burger {
  width: 30px;
  height: 20px;
  position: relative;
}

.header-burger span {
  content: "";
  width: 100%;
  height: 2px;
  background-color: #fff;
  position: absolute;
  top: 9px;
  left: 0;
}

.header-burger:after, .header-burger:before {
  content: "";
  width: 100%;
  height: 2px;
  background-color: #fff;
  position: absolute;
  left: 0;
}

.header-burger:before {
  top: 0;
}

.header-burger:after {
  bottom: 0;
}

.header-list {
  gap: 28px;
  display: flex;
}

.header-link {
  color: #000;
  font-size: 16px;
  font-style: normal;
  font-weight: 400;
  line-height: normal;
}

@media screen and (max-width: 767.98px) {
  .header-order-btn {
    display: none;
  }

  .header-menu {
    width: 100%;
    height: 100%;
    background-color: var(--yellow-main);
    position: fixed;
    top: 0;
    left: 0;
  }
}

@media screen and (min-wigth: 768px) {
  .header-burger {
    display: none;
  }
}



